.container {
    display: -ms-flexbox;
    display: flex;
    position: fixed; /* Set the navbar to fixed position */
    top: 0; /* Position the navbar at the top of the page */
    width: 100%;
}

.container > li {
    -ms-flex: 1 1;
        flex: 1 1;
    display: inline;
  }

  li {
      display: inline;
  }
  @media all and (max-width: 600px) {
  
    .container {
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
    }
    
    .container > li {
      -ms-flex-preferred-size: 50%;
          flex-basis: 50%;
    }
  }

  @media all and (max-width: 400px) {
    .container > li {
      -ms-flex-preferred-size: 100%;
          flex-basis: 100%;
    }
    .search {
      -ms-flex-order: 1;
          order: 1;
    }
  }


